<template>
    <div class="use-number">
        <h3 class="mb-10">Data Nomor</h3>
        <vx-card>
            <div class="vx-row mb-10">
                <div class="vx-col w-full">
                    <!-- <table>
                        <thead>
                            <tr>
                                <th style="width: 5px"><vs-checkbox v-model="selectAll"></vs-checkbox></th>
                                <th>Nama</th>
                                <th>Grup</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in filteredItems" :key="index" >
                                <td><vs-checkbox v-model="selected" :vs-value="item.number"></vs-checkbox></td>
                                <td>{{item.number}}</td>
                                <td class="text-center">{{item.grup }}</td>
                            </tr>
                        </tbody>
                    </table> -->
                    <vs-table
                    :data="number"
                    pagination search>
                        <template slot="header">
                            <vx-tooltip text="Tambah Nomor" style="display:inline">   
                                <vs-button @click="addNomor = true" class="float-right mb-3 mr-2" color="#0fabbc" icon="add_circle_outline"> Tambah Nomor</vs-button>
                            </vx-tooltip>
                            <vx-tooltip text="Tambah Grup" style="display:inline">   
                                <vs-button @click="addGrup = true" class="float-right mb-3" color="#0c8896" icon="add_circle_outline"> Tambah Grup</vs-button>
                            </vx-tooltip>
                        </template>
                        <template slot="thead" >
                            <!-- <vs-th style="width: 5px" class="bg-primary text-white text-center">
                            <vs-checkbox v-model="selectAll"></vs-checkbox>
                            </vs-th> -->
                            <vs-th sort-key="nama" class="bg-primary text-white text-center">
                                <div class="mx-auto">Nama</div>
                            </vs-th>
                            <vs-th sort-key="grup" class="bg-primary text-white text-center" >
                                <div class="mx-auto">Grup</div>
                            </vs-th>
                        </template>

                        <template slot-scope="{data}">
                            <vs-tr :data="item" :key="index" v-for="(item, index) in data" >
                            <!-- <vs-td><vs-checkbox v-model="selected" :vs-value="item.number"></vs-checkbox></vs-td> -->
                            <vs-td>{{item.number}}</vs-td>
                            <vs-td>{{item.grup }}</vs-td>
                            </vs-tr>
                        </template>
                    </vs-table>
                </div>
            </div>
        </vx-card>

        <!-- Add Informasi -->
        <vs-popup class="holamundo"  title="Tambah Nomor" :active.sync="addNomor">
            <CreateNumber />
        </vs-popup>

        <!-- Add Informasi -->
        <vs-popup class="holamundo"  title="Tambah Grup" :active.sync="addGrup">
            <CreateGrup />
        </vs-popup>

    </div>
</template>

<script>
import CreateNumber from './add-number.vue'
import CreateGrup from './add-group.vue'

export default {
    data () {
        return {
            addNomor: false,
            addGrup: false,
            search: '',
            selected: [],
            number: [
                {
                    number: 6285697073132,
                    grup: 'Area 1'
                },
                {
                    number: 6285697980090,
                    grup: 'Area 1'
                },
                {
                    number: 6285697023099,
                    grup: 'Area 2'
                },
                {
                    number: 6285691230981,
                    grup: 'Area 2'
                }
            ]
        }
    },

    components: {CreateNumber, CreateGrup},

    computed:{
        selectAll: {
            get () {
                return this.selected.length === this.number.length
            },
            set (value) {
                this.selected = []

                if (value) {
                    this.number.forEach((select) => {
                        this.selected.push(select.number)
                    })
                }
            }
        },
    
        filteredItems() {
            return this.number.filter(item => {
                if(item.number == undefined || item.grup == undefined)
                return false
                return item.grup.toLowerCase().indexOf(this.search.toLowerCase()) > -1
            })
        }
    },
}
</script>

<style scoped>
    .table-wrapper{
  overflow-y: scroll;
  height: 560px;
}

.table-wrapper th{
    position: sticky;
    top: 0;
    z-index: 1;
}

table{
  border-collapse: collapse;
  width: 100%;
}

th{
    font-size: 12px;
    background: #df4759;
    color: #fff;
    border: solid 1px #DDEFEF;
    padding: 5px;
    text-align: center;
    white-space: nowrap;
}

table th.sorted.asc::after {
	display: inline-block;
	content: ' ▼';
}

 table th.sorted.desc::after {
	display: inline-block;
	content: ' ▲';
}

td,th{
  padding: 5px;
  white-space: nowrap;
  border: solid 1px #DDEFEF;
}
</style>

